onal del trabajo OIT habla de pérdidas de puestos de trabajo alrededor de uno 345 millones. En Colombia, la pérdida de puestos de trabajo oscila entre los 1,5 millones, observa los elementos a tener presente frente a este balance realizado: ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"cajon color-primario p-4 mb-4"},[_c('p',[_vm._v("Gracias al tamaño de su mercado y la extensión de sus recursos naturales: café, esmeraldas, petróleo y carbón, entre otros. Además de una reputación histórica como un deudor ejemplar, Colombia ha experimentado un crecimiento estable y sólido durante la mayor parte de las últimas dos décadas. Si bien el país se vio afectado por la caída de los precios del petróleo debido a la pandemia en 2020, Colombia pudo recuperarse en 2021, con un crecimiento del producto interno bruto (PIB) principalmente debido al impulso de las exportaciones de petróleo y al aumento del consumo de los hogares, que debería continuar para impulsar la economía en los próximos años, apoyado en la continua recuperación del mercado laboral y la continua implementación de programas de apoyo fiscal dirigidos a los hogares de bajos ingresos.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-12"},[_c('p',[_vm._v("El repunte del consumo de los hogares del país impulsó las importaciones, que superaron el aumento de las exportaciones a pesar del aumento de los precios de las materias primas agrícolas y energéticas, y contribuyó al déficit de cuenta de Colombia, de esta manera: ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"c